import React, { Component } from 'react';
import { connect } from 'react-redux';
import './Contact.css';
import { sendEmail, setFormEmail, setFormName, setFormWebsite, setFormMessage, setErrorMsg } from '../../actions';

const mapStateToProps = state => {
    return {
        formEmail: state.handleContactForm.formEmail,
        formName: state.handleContactForm.formName,
        formMessage: state.handleContactForm.formMessage,
        formWebsite: state.handleContactForm.formWebsite,
        isSent: state.handleContactForm.isSent,
        errorMsg: state.handleContactForm.errorMsg
    }
}
  
const mapDispatchToProps = (dispatch) => {
    return {
        onContactFormSubmit: () => dispatch(sendEmail()),
        onEmailInputChange: (event) => dispatch(setFormEmail(event.target.value)),
        onNameInputChange: (event) => dispatch(setFormName(event.target.value)),
        onWebsiteInputChange: (event) => dispatch(setFormWebsite(event.target.value)),
        onMessageInputChange: (event) => dispatch(setFormMessage(event.target.value)),
        setErrorMsg: (text) => dispatch(setErrorMsg(text))
    }
}

class Contact extends Component {
    render() {
        const { formEmail, formName, formMessage, formWebsite, onContactFormSubmit, onEmailInputChange, onNameInputChange, onWebsiteInputChange, onMessageInputChange, setErrorMsg, isSent, errorMsg  } = this.props;
        
        return (
            <div className="section section-small section-get-started" id="contact">
                <div className="parallax filter filter-color-red">
                    <div className="image" id="office-1">
                    </div>
                    <div className="container">
                        <div className="title-area">
                            <h2 className="text-white">Contact Me!</h2>
                            <div className="separator line-separator">♦</div>
                        </div>
                        
                        <div className="contactform">  
                            <form id="contact_form" className="br3" action="" method="post">
                            <div className="form_top">
                                <div className="form_left">
                                    <input onChange={onNameInputChange} placeholder="Your name" type="text" tabIndex="1" value={formName} required />
                                    <input onChange={onEmailInputChange} placeholder="Your Email Address" type="email" tabIndex="2" value={formEmail} required />
                                    <input onChange={onWebsiteInputChange} placeholder="Your Web Site (optional)" type="url" tabIndex="4" value={formWebsite} />
                                </div>
                                <div className="form_right">
                                    <textarea onChange={onMessageInputChange} placeholder="Type your message here...." tabIndex="5" required value={formMessage}></textarea>
                                </div>
                            </div>
                            <div className="button-get-started form_send">
                                    {isSent 
                                     ? <a href="#contact" className="btn btn-primary btn-fill btn-m not-allowed">Message Sent</a>
                                     : <a onClick={formName && formEmail && formMessage ? onContactFormSubmit : () => setErrorMsg('Please fill in the required fields.') } href="#contact" className="btn btn-danger btn-fill btn-m">Send</a>
                                    }
                                    <div id="form_warning">
                                    { errorMsg ? errorMsg : null }
                                    </div>
                            </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Contact);