import {EMAIL_SENT_SUCCESSFUL,
        EMAIL_SENT_FAILED,
        CHANGE_FORM_EMAIL,
        CHANGE_FORM_NAME,
        CHANGE_FORM_WEBSITE,
        CHANGE_FORM_MESSAGE,
        CHANGE_ERROR_MESSAGE } from './constants';

const initialStateContactForm = {
    formEmail: '',
    formName: '',
    formWebsite: '',
    formMessage: '',
    isSent: false,
    isError: false,
    errorMsg: ''
}

export const handleContactForm = (state = initialStateContactForm, action={}) => {
    switch(action.type) {
        case EMAIL_SENT_SUCCESSFUL:
            return { ...state, isSent: true, isError: false, formEmail: '', formName: '', formWebsite: '', formMessage: '', errorMsg: '' }
        case EMAIL_SENT_FAILED:
            return { ...state, isSent: false, isError: true, errorMsg: 'Error sending form! Please resend.' }
        case CHANGE_FORM_EMAIL:
            return { ...state, formEmail: action.payload }
        case CHANGE_FORM_NAME:
            return { ...state, formName: action.payload }
        case CHANGE_FORM_WEBSITE:
            return { ...state, formWebsite: action.payload }
        case CHANGE_FORM_MESSAGE:
            return { ...state, formMessage: action.payload }
        case CHANGE_ERROR_MESSAGE:
            return { ...state, errorMsg: action.payload }
        default:
            return state;
    }
}