import * as emailjs from 'emailjs-com';

import {EMAIL_SENT_SUCCESSFUL,
        EMAIL_SENT_FAILED,
        CHANGE_FORM_EMAIL,
        CHANGE_FORM_NAME,
        CHANGE_FORM_WEBSITE,
        CHANGE_FORM_MESSAGE,
        CHANGE_ERROR_MESSAGE } from './constants';


export const setFormEmail = (text) => ({
    type: CHANGE_FORM_EMAIL,
    payload: text
})

export const setFormName = (text) => ({
    type: CHANGE_FORM_NAME,
    payload: text
})

export const setFormWebsite = (text) => ({
    type: CHANGE_FORM_WEBSITE,
    payload: text
})

export const setFormMessage = (text) => ({
    type: CHANGE_FORM_MESSAGE,
    payload: text
})

export const setErrorMsg = (text) => ({
    type: CHANGE_ERROR_MESSAGE,
    payload: text
})

export const sendEmail = () => (dispatch, getState) => {
    const { formEmail, formName, formWebsite, formMessage } = getState().handleContactForm;
    
    emailjs.send('mailgun',process.env.REACT_APP_EMAILJS_TEMPLATEID, { email: formEmail, name: formName, website: formWebsite, message: formMessage }, process.env.REACT_APP_EMAILJS_USERID)
        .then(response => dispatch({ type: EMAIL_SENT_SUCCESSFUL, payload: response  }))
        .catch(err => dispatch({ type: EMAIL_SENT_FAILED, payload: err  }));
}