import React, { Component } from 'react';
import TextyAnim from 'rc-texty';
import TweenOne from 'rc-tween-one';
import 'rc-texty/assets/index.css';

class Header extends Component {
    geInterval = (e) => {
        switch (e.index) {
          case 0:
            return 0;
          case 1:
            return 150;
          case 2:
          case 3:
          case 4:
          case 5:
          case 6:
            return 150 + 450 + (e.index - 2) * 10;
          default:
            return 150 + 450 + (e.index - 6) * 150;
        }
      }
    getEnter = (e) => {
        const t = {
          opacity: 0,
          scale: 0.8,
          y: '-100%',
        };
        if (e.index >= 2 && e.index <= 6) {
          return { ...t, y: '-30%', duration: 150 };
        }
        return t;
      }
    
    getSplit = (e) => {
        const t: string[] = e.split(' ');
        const c = [];
        t.forEach((str, i) => {
          c.push((
            <span key={`${str}-${i}`}>
              {str}
            </span>
          ));
          if (i < t.length - 1) {
            c.push(<span key={` -${i}`}> </span>);
          }
        });
        return c;
      }
      
      render() {
        return (
            <div className="section section-header">
                <div className="parallax filter filter-color-red">
                    <div id="header-1" className="image"></div>
                    <div className="container mt4">
                        <div className="content">
                            <div className="title-area">
                                <div className="combined">
                                    <div className="combined-shape">
                                        <div className="shape-left">
                                            <TweenOne
                                                animation={[
                                                { x: 158, type: 'from', ease: 'easeInOutQuint', duration: 600 },
                                                { x: -158, ease: 'easeInOutQuart', duration: 450, delay: -150 },
                                                ]}
                                            />
                                        </div>
                                        <div className="shape-right">
                                        <TweenOne
                                            animation={[
                                            { x: -158, type: 'from', ease: 'easeInOutQuint', duration: 600 },
                                            { x: 158, ease: 'easeInOutQuart', duration: 450, delay: -150 },
                                            ]}
                                        />
                                        </div>
                                    </div>
                                    <TextyAnim
                                        className="content"
                                        type="bottom"
                                        split={this.getSplit}
                                        delay={400}
                                        interval={30}
                                    >
                                        Hi!
                                    </TextyAnim>
                                    <TextyAnim
                                        className="title"
                                        type="mask-top"
                                        delay={1500}
                                        enter={this.getEnter}
                                        interval={this.geInterval}
                                        component={TweenOne}
                                        componentProps={{
                                        animation: [
                                            { x: 130, type: 'set' },
                                            { x: 100, delay: 500, duration: 450 },
                                            {
                                            ease: 'easeOutQuart',
                                            duration: 300,
                                            x: 0,
                                            },
                                            {
                                            letterSpacing: 0,
                                            delay: -300,
                                            scale: 6.0,
                                            ease: 'easeInOutQuint',
                                            duration: 1000,
                                            },
                                            { scale: 3.0, width: '100%', delay: -300, duration: 1000, ease: 'easeInOutQuint' },
                                        ],
                                        }}
                                    >
                                        {`I'm Valerie.`}
                                    </TextyAnim>
                                    <TweenOne
                                        className="combined-bar"
                                        animation={{ delay: 2000, width: 0, x: 158, type: 'from', ease: 'easeInOutExpo' }}
                                    />
                                    <TextyAnim
                                        className="content"
                                        type="bottom"
                                        split={this.getSplit}
                                        delay={2200}
                                        interval={30}
                                    >
                                        I develop web, IOS, and MacOS applications.
                                    </TextyAnim>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Header;